.shop__products {
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 30px;
  row-gap: 90px;
  place-items: center;
  padding: 30px 0 90px 0;

  @media only screen and (max-width: 1024px) {
    grid-template-columns: 1fr 1fr;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media only screen and (max-width: 425px) {
    grid-template-columns: 1fr;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.product {
  border-radius: 15px;
  // width: 300px;
  // height: 300px;
  // margin: 100px;
  // padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;

  @media only screen and (max-width: 768px) {
    // height: 300px;
  }
}

.product__image {
  width: 400px;
  height: 350px;
  object-fit: contain;
  @media only screen and (max-width: 768px) {
    height: 200px;
    margin-bottom: 20px;
  }
  @media only screen and (max-width: 425px) {
    height: 150px;
    margin-bottom: 10px;
  }
}

.product__description {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.product__name {
  font-weight: 700;
}

.product__price {
  font-size: 24px;
}

.product__addToCartBttn {
  font-weight: 500;
  font-size: 20px;
  background-color: transparent;
  border: 2px solid var(--maincolor);

  min-width: 100px;
  padding: 7px 20px;

  border-radius: 20px;
  transition: background-color 0.2s ease-in, color 0.2s ease-in;

  &:hover {
    background-color: var(--maincolor);
    color: white;
    cursor: pointer;
  }
}

